@font-face {
  font-family: "Cabinet Grotesk";
  src: url(/project-11/static/media/CabinetGrotesk-Medium.cba8be79.woff);
  font-style: normal;
  font-weight: 500;
}

@font-face {
  font-family: "Cabinet Grotesk";
  src: url(/project-11/static/media/CabinetGrotesk-Bold.983a4202.woff);
  font-style: normal;
  font-weight: 700;
}
body {
  margin: 0;
  font-family: 'Cabinet Grotesk', 'Arial', 'Helvetica', 'sans-serif';
  font-size: 20px;
  line-height: 24px;
  background-color: #DCDCDC;
}


.App {
  max-width: 1360px;
  margin: 32px auto;
}
.header {
  display: flex;
  justify-content: space-between;
  height: 40px;
  width: 100%;
  font-weight: 700;
  margin-bottom: 28px;
}

.header__left-side,
.header__right-side {
  display: flex;
  align-items: center;
  margin: 0;
}

.header__left-side {
  grid-gap: 24px;
  gap: 24px;
}

.header__add-button {
  font-family: "Cabinet Grotesk", Arial, Helvetica, sans-serif;
  font-size: 20px;
  font-weight: 700;
  margin-right: 20px;
  margin-left: 20px;
  background-color: transparent;
  border: none;
}

.header__add-button:hover,
.header__user-name:hover {
  cursor: pointer;
}

.header__user-name {
  margin-right: 88px;
  color: rgba(0, 0, 0, 1);
  text-decoration: none;
}
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #fff;
  transition: .4s;
  border: 2px solid #7E7E7E;
  width: 56px;
  height: 28px;
  transition: .4s;
  z-index: 1;
}

.slider:hover {
  border: 2px solid rgba(0, 0, 0, 1);
}

.slider:before {
  position: absolute;
  content: "";
  height: 28px;
  width: 28px;
  left: 0px;
  bottom: 0px;
  background-color: #000;
  transition: .4s;
}

input:checked+.slider .celsius {
  color: #FFF;
}

input:checked+.slider .fahrenheit {
  color: rgba(0, 0, 0, 0.5);
}

input+.slider .fahrenheit {
  /* background-color: #2196F3; */
  color: #FFF;
}

input:focus+.slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked+.slider:before {
  -webkit-transform: translateX(28px);
  transform: translateX(28px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.fahrenheit,
.celsius {
  font-family: 'Cabinet Grotesk';
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 17px;
  color: rgba(0, 0, 0, 0.5);
  align-items: center;
  z-index: 10;
}
