.card__section {
  width: 100%;
  padding: 0;
}

.card__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  grid-gap: 19px;
  gap: 19px;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.card {
