  border-radius: 20px;
  list-style: none;
  position: relative;
  display: flex;
  justify-content: center;
  background-position: center;
  background-size: contain;
  mix-blend-mode: multiply;
  cursor: pointer;
  box-sizing: border-box;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.card__heading {
