  padding: 20px 0px 20px 16px;
  box-sizing: border-box;
  background-size: cover;
  background-repeat: no-repeat;
}

.weather__temp {
  color: #FFFFFF;
  font-size: 36px;
  font-weight: 700;
  line-height: 40px;
  margin: 0;
  padding: 0;
}
.profile__wrapper {
