  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 25px;
}

.profile__clothes-section {
  width: 75%;
}

.profile__main-header {
  display: flex;
  margin-bottom: 16px;
}

.profile__add-button {
  color: rgba(0, 0, 0, 0.6);
  font-family: "Cabinet Grotesk", Arial, Helvetica, sans-serif;
  font-size: 20px;
  font-weight: 700;
  line-height: 25px;
  margin-right: 20px;
  margin-left: 20px;
  background-color: transparent;
  border: none;
}

.profile__add-button:hover {
  cursor: pointer;
}

.profile__card-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  grid-gap: 20px;
  gap: 20px;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.profile__card {
  width: 325px;
  height: 328px;
  background: #E9E9E9;
  border: 1px solid rgba(0, 0, 0, 0.3);
  border-radius: 20px;
  list-style: none;
  position: relative;
  display: flex;
  justify-content: center;
  background-position: center;
  background-size: contain;
  mix-blend-mode: multiply;
  cursor: pointer;
  box-sizing: border-box;
}
.popup {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0%;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transition: visibility 0s 0.5s, opacity 0.5s linear;
}

.popup_opened {
  visibility: visible;
  opacity: 1;
  pointer-events: all;
  transition: visibility 0s, opacity 0.5s linear;
}
.popup__delete-button {
  color: #FF4D00;
  font-family: 'Cabinet Grotesk';
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  border: none;
  background-color: transparent;
  position: absolute;
  right: 30px;
  bottom: 68px;
}

.popup__delete-button:hover,
.popup__close-button:hover,
.popup__confirm-delete-button:hover,
.popup__cancel-button {
  cursor: pointer;
}

.popup__close-button {
  height: 30px;
  width: 30px;
  position: absolute;
  top: 20px;
  right: 20px;
  padding: 0;
  border: none;
  background-color: transparent;
}

.popup__close-icon {
  height: 20px;
  width: 20px;
}

/* Popup Form Elements */
.popup__form {
  width: 496px;
  height: 436px;
  padding: 28px 28px 36px 28px;
  margin: 193px auto;
  background-color: #FFFFFF;
  border-radius: 10px;
  position: relative;
  box-sizing: border-box;
}

.popup__form-heading {
  font-family: 'Cabinet Grotesk';
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  margin-top: 0;
  margin-bottom: 24px;
}

.popup__form-label {
  display: block;
  height: 52px;
  margin-bottom: 24px;
  color: #000000;
  font-family: 'Cabinet Grotesk';
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
}

.popup__form-input {
  height: 27px;
  width: 100%;
  margin-top: 0;
  margin-bottom: 0px;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 1px solid rgba(0, 0, 0, 1);
}

.popup__form-radio-section {
  margin-bottom: 32px;
}

.popup__form-radio-heading {
  padding: 0px;
  margin: 0px 0px 12px 0px;
  color: #000000;
  font-family: 'Cabinet Grotesk';
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
}

.popup__form-radio-list {
  margin: 0;
  padding: 0;
}

.popup__form-radio-element {
  list-style-type: none;
  margin-bottom: 8px;
}

.popup__form-radio-element:nth-child(3) {
  margin-bottom: 0;
}

.popup__form-radio-label {
  font-family: 'Cabinet Grotesk';
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  color: rgba(0, 0, 0, 0.5);
  margin: 0px;
  opacity: 0.5;
}

.popup__form-radio-label_checked {
  opacity: 1;
}

.popup__form-radio {
  margin-right: 11px;
  margin-left: 0px;
}

.popup__form-add-button {
  display: flex;
  justify-content: center;
  margin: 0px;
  width: 124px;
  height: 36px;
  padding: 8px 16px;
  color: #FFFFFF;
  background: #000000;
  border-radius: 2px;
  border-style: none;
}

.popup__form-add-button:hover:not(:disabled) {
  cursor: pointer;
  opacity: 0.8;
}

.popup__form-add-button_disabled {
  opacity: 0.2;
  background-color: transparent;
  color: #000000;
  border: 1px solid #000000;
}

.popup__delete-item-form {
  width: 670px;
  height: 280px;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 28px 28px 36px 28px;
  margin: 255px auto 0px;
  background-color: #FFFFFF;
  border-radius: 10px;
  position: relative;
  box-sizing: border-box;
}

.popup__confirm-delete-button,
.popup__cancel-button {
  font-family: 'Cabinet Grotesk';
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  background: transparent;
  border: none;
}

.popup__confirm-text {
  margin-bottom: 40px;
}

.popup__confirm-delete-button {
  margin-bottom: 24px;
  color: rgba(255, 77, 0, 1);
}
